/* Captcha Styles */
.captcha-container {
  margin: 15px 0;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.captcha-display {
  font-size: 24px;
  font-weight: bold;
  letter-spacing: 3px;
  padding: 10px;
  background-color: #e9ecef;
  border-radius: 5px;
  margin: 10px 0;
  font-family: monospace;
  text-decoration: line-through;
  color: #333;
  text-align: center;
}

.captcha-controls {
  display: flex;
  gap: 10px;
  align-items: center;
}

.captcha-input {
  flex: 1;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.verify-button {
  padding: 8px 15px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.refresh-button {
  padding: 8px 15px;
  background-color: #6c757d;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.captcha-success {
  color: #28a745;
  margin-top: 10px;
  font-weight: bold;
}

/* Login Captcha */
.login-captcha {
  margin: 15px 0;
  padding: 15px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  background-color: #f8f9fa;
}

.captcha-header label {
  font-weight: bold;
  margin-bottom: 8px;
  display: block;
}

.captcha-content {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.captcha-input-group {
  display: flex;
  gap: 10px;
}

.refresh-captcha {
  background: none;
  border: none;
  color: #007bff;
  cursor: pointer;
  font-size: 12px;
  align-self: flex-start;
}

/* Credentials Display */
.credentials-container {
  text-align: center;
}

.credentials-card {
  background: #fff3cd;
  border: 1px solid #ffeaa7;
  border-radius: 8px;
  padding: 20px;
  margin: 20px 0;
  text-align: left;
}

.credential-item {
  margin: 10px 0;
  padding: 8px;
  background: white;
  border-radius: 5px;
  border: 1px solid #ddd;
}

.credential-value {
  background: #f8f9fa;
  padding: 4px 8px;
  border-radius: 3px;
  margin-left: 10px;
  font-family: monospace;
}

.credential-warning {
  background: #f8d7da;
  color: #721c24;
  padding: 10px;
  border-radius: 5px;
  margin-top: 15px;
  font-size: 14px;
}

/* Login Type Selector */
.login-type-selector {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
}

.type-option {
  flex: 1;
  text-align: center;
  padding: 10px;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s;
}

.type-option.active {
  border-color: #007bff;
  background-color: #e7f3ff;
}

.type-option input {
  margin-right: 5px;
}

/* Responsive */
@media (max-width: 768px) {
  .captcha-controls {
    flex-direction: column;
  }
  
  .login-type-selector {
    flex-direction: column;
  }
}